    margin-top: 10px;
    margin-bottom: 0;
  }
}

#origamiez-main-content {
  margin: 0 auto;
  width: 100%;
}

#origamiez-header-top {
  padding-top: 60px;
  padding-bottom: 60px;
}

#origamiez-header-bottom {
  position: relative;
  margin-bottom: 1px;
}

#origamiez-logo {
  > a {
    @include inline-block();
  }
  img {
    max-width: 100%;
    height: auto;
