}

#origamiez-header-bar {
  height: 35px;
}
/*
 * --------------------------------------------------
 * HEADER SEARCH BOX
 * --------------------------------------------------
 */
#main-nav-inner {
  position: relative;
  min-height: 46px;

  #origamiez-header-search-box {
    position: absolute;
    top: 0;
    right: 10px;
    height: 46px;
    line-height: 46px;
    width: 341px;

    form#search-form {
      height: 46px;
      line-height: 46px;

      .search-submit {
        top: 0;
        right: 0;
        height: 46px;
        width: 46px;
        line-height: 46px;
      }
    }
  }
}

/*
 * --------------------------------------------------
 * MAIN MENU
 * --------------------------------------------------
 */
#main-nav {
  width: 100%;

  &.stuckMenu.isStuck {
    z-index: 999999;
  }
}

#main-menu {
  display: block;
  padding: 0;
  margin: 0;
  list-style-type: none;

  .sub-menu {
    display: none;
  }

  a {
    text-decoration: none;
    outline: none;
  }

  > li {
    display: inline-block;
    *display: inline-block;

    &.origamiez-menuitem-first {
      > a {
        padding-left: 0;
      }
    }

    &.menu-item-has-children {
      > a:after {
        display: block;
        position: absolute;
        right: 0;
        top: 0;
        height: 46px;
        width: 16px;
      }
    }

    > a {
      display: block;
      height: 46px;
      text-transform: uppercase;
      position: relative;
      padding-left: 20px;
      padding-right: 20px;
    }
  }

  li {
    position: relative;

    ul {
      display: block;
      position: absolute;
      top: 46px;
      left: -1px;
      z-index: 50;
      width: 250px;
      list-style-type: none;
      margin: 0;
      padding: 0 5px;

      li {
        list-style-type: none;
        margin: 0 0 -1px 0;
        padding: 0;

        &.menu-item-has-children {
          > a:after {
            display: block;
            position: absolute;
            right: 0;
            top: 0;
            height: 40px;
            width: 16px;
          }
        }

        a {
          display: block;
          width: 100%;
          height: 40px;
          line-height: 30px;
          padding: 5px 15px;
          overflow: hidden;
          position: relative;
        }

        ul {
          top: -3px;
          left: 235px;
        }
      }
    }
  }
}

#origamiez-footer-sidebars {
  #origamiez-footer-sidebars-inner {
    padding: 40px 0 20px 0;

    .widget {
      margin-bottom: 20px;

      .widget-title {
        border-bottom: none;
        padding-left: 0;
      }

      .origamiez-widget-content {
        padding: 0;
      }
    }
  }
}

#origamiez-footer-end {
  padding-left: 20px;
  padding-right: 20px;
  min-height: 50px;
  height: auto;
  line-height: 50px;
}

#bottom-menu {
  text-align: center;
  margin: 0;
  padding: 0;

  a {
    text-decoration: none;
    outline: none;
  }

  > li {
    display: inline-block;
    height: 13px;
    line-height: 13px;
    vertical-align: middle;

    > a {
      display: block;
      margin: 0 0 0 13px;
      padding: 0 0 0 13px;
      line-height: 13px;
      height: 13px;
    }
  }
}

