.origamiez-mobile-icon {
  display: none;
  height: 44px;
  line-height: 44px;
  text-align: center;
  z-index: 10;
  padding: 0 10px;
  margin: 1px 0 1px 0;

  span {
    &.fa {
      display: inline-block;
      margin-right: 10px;
    }
  }

  cursor: pointer;
}

#mobile-menu {
  margin: 0;
  padding: 60px 20px 20px 20px;

  ul, li {
    list-style: none;
    margin: 0;
    padding-left: 20px;
  }

  li {
    line-height: 20px;
    padding: 10px 0 0 0;
    margin: 10px 0 0 0;
  }

  & > li:first-child {
    margin-top: 0;
    padding-top: 0;
  }

  a {
    display: block;
    text-decoration: none;
    position: relative;

    & > span {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      width: 20px;
      z-index: 5;
      text-align: right;
    }
  }
}

