    padding: 0;

    .item {
      position: relative;

      .caption {
        display: block;
        width: 80%;
        padding: 20px 20px 20px 15px;
        position: absolute;
        bottom: 20px;
        left: 0;

        .metadata,
        .entry-excerpt {
          margin-top: 15px;
          margin-bottom: 0;
        }
      }

      &.item-bottom {
        margin-top: 5px;
      }
    }

    .row {
      margin-left: -2.5px;
      margin-right: -2.5px;

      .col-left,
      .col-right {
        padding-left: 2.5px;
        padding-right: 2.5px;
      }
    }

    .col-right {
      position: relative;

      .owl-pagination {
        padding: 0 10px;
        margin: 5px 15px 0 0;
        height: 20px;
        line-height: 20px;
        @include border-radius(10px);
      }

      .owl-controls {
        position: absolute;
        top: 0;
        right: 0;

        .owl-page {
          span {
            margin: 5px;
            height: 10px;
          }
        }
      }
    }
  }
}

