/*
 * --------------------------------------------------
 * SINGLE CAROUSEL
 * --------------------------------------------------
 */
.owl-single-carousel {
  position: relative;
}

.owl-theme {
  .owl-controls {
    .owl-pagination {
      .owl-page {
        span {
          width: 10px;
          height: 10px;
