    padding-bottom: 30px;

    .origamiez-admin-rating-detail {
      h3 {
        margin-bottom: 20px;
      }
    }
  }

  .origamiez-rating-total {
    margin-right: 30px;
    @extend .border-circle;
    height: 96px;
    width: 96px;
    text-align: center;
    position: relative;

    span {
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      line-height: 96px;
      height: 96px;
      width: 96px;
      font-size: 24px;
    }
  }

  .origamiez-admin-rating-per-featured {
    .row {
      margin-top: 15px;
    }

    .col-right {
      height: 56px;
      position: relative;

      .caption,
      .circle,
      .line {
        display: block;
        position: absolute;
      }

      .circle {
        top: 0;
        left: 0;
        height: 56px;
        width: 56px;
        z-index: 10;
        @extend .border-circle;
      }

      .line {
        top: 50%;
        left: 0;
        height: 3px;
        z-index: 5;
        margin-top: -1.5px;

        &.line-front {
          z-index: 5;
          width: 0;
        }

        &.line-back {
          z-index: 1;
          width: 100%;
        }
      }

      .caption {
        display: none;
        top: 0;
        right: 0;
        text-align: right;
        width: 100%;
      }
    }
  }
}

.origamiez-rating-total-inside-widget,
.origamiez-rating-total-inside-widget > span {
  display: block;
  width: 60px;
  height: 60px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -30px;
  margin-left: -30px;
  @extend .border-circle;
}

.border-circle {
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  behavior: url(css/pie.htc);
}

.border-square {
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
  behavior: url(css/pie.htc);
}

.border-circle-s {
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  border-radius: 2px;
  behavior: url(css/pie.htc);
}

