body.origamiez-header-style-up-down {
  #origamiez-header-top {
    img#site-logo {
      @include inline-block();
    }

    a#site-home-link,
    #origamiez-logo,
    #origamiez-top-banner {
      text-align: center;
    }

    #origamiez-top-banner {
      margin-top: 30px;
    }
  }

  ul#main-menu {
    text-align: center;

    ul {
      text-align: left;
    }
  }
}

/*
 * --------------------------------------------------
 * UPDATE :: 2015.07.07 (1.2.2)
 * --------------------------------------------------
 */
#origamiez_singular_pagination {
  a {
    @include inline-block();
    height: 25px;
    padding: 0 15px;
    margin-left: 20px;
  }
}

body.page-template-template-page-three-cols,
body.origamiez-taxonomy-three-cols,
body.origamiez-single-post-three-cols {
  #sidebar-middle {
    padding-left: 20px;
    padding-right: 20px;
  }
}

body.page-template-template-page-three-cols-slm,
body.origamiez-taxonomy-three-cols-slm,
body.origamiez-single-post-three-cols-slm {
  #sidebar-middle-clone,
  #sidebar-middle {
    padding-right: 20px;
  }

  #sidebar-center {
    padding-left: 20px;
  }
}

#sidebar-middle,
#sidebar-middle-clone {
  .origamiez-widget-content {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

body.origamiez-missing-sidebar-right {
  &.origamiez-single-post-two-cols,
  &.origamiez-taxonomy-two-cols {
    #sidebar-center {
      width: 100%;
      padding-right: 0;
      border-right: none;
    }
  }

  &.origamiez-single-post-three-cols,
  &.origamiez-taxonomy-three-cols {
    #sidebar-center {
      width: 70%;
    }

    #sidebar-middle {
      width: 30%;
      padding-right: 0;
      border-right: none;
    }
  }

  &.origamiez-single-post-three-cols-slm,
  &.origamiez-taxonomy-three-cols-slm {
    #sidebar-center {
      width: 70%;
      padding-right: 0;
      border-right: none;
    }

    #sidebar-middle {
      width: 30%;
    }
  }

  &.origamiez-missing-sidebar-left {
    &.origamiez-single-post-three-cols,
    &.origamiez-taxonomy-three-cols,
    &.origamiez-single-post-three-cols-slm,
    &.origamiez-taxonomy-three-cols-slm {
      #sidebar-center {
        width: 100%;
        padding-right: 0;
        border-right: none;
        padding-left: 0;
        border-left: none;
      }
    }
  }
}

body.origamiez-missing-sidebar-left {
  &.origamiez-single-post-three-cols,
  &.origamiez-taxonomy-three-cols,
  &.origamiez-single-post-three-cols-slm,
  &.origamiez-taxonomy-three-cols-slm {
    #sidebar-center {
      width: 70%;
    }
  }

  &.origamiez-single-post-three-cols-slm,
  &.origamiez-taxonomy-three-cols-slm {
    #sidebar-center {
      padding-left: 0;
      border-left: none;
    }
  }

}

body.origamiez-page-magazine {
  &.origamiez-missing-sidebar-right {
    #sidebar-center {
      width: 100%;
      padding-right: 0;
      border-right: none !important;
    }
  }
}

.fw-container {
  padding-left: 0;
  padding-right: 0;
}
